// =============================================================================
// SMF DEBUG CLIENT
// =============================================================================
// Client helper for fetching SMF facilitator debug data

import type { FacilitatorPathMetricsTimeframe } from '@/infra/x402scan/types';

// Re-export timeframe type
export type { FacilitatorPathMetricsTimeframe };

// =============================================================================
// TYPES
// =============================================================================

/** Activity level derived from Scattering metrics */
export type ActivityLevel = 'high' | 'medium' | 'low' | 'none';

/** x402scan observability metrics */
export interface FacilitatorMetrics {
  successRate: number;
  errorRate: number;
  p95LatencyMs?: number;
  invocations: number;
  dataAgeHours: number;
}

/** Scattering volume/activity metrics */
export interface ScatteringDisplayMetrics {
  volumeUsd3d: number;
  txCount3d: number;
  uniqueBuyers3d: number;
  uniqueSellers3d: number;
  volumeChangeRate?: number;
  activityLevel: ActivityLevel;
  activityScore: number;
  fetchedAt?: string;
}

/** NexFlow internal traffic metrics */
export interface NexFlowTrafficMetrics {
  last24hInvocations: number;
  last24hDogfoodInvocations: number;
  last24hExternalInvocations: number;
  last24hSuccessRate?: number;
  last24hP95LatencyMs?: number;
}

/** External links for debugging */
export interface ExternalLinks {
  x402scan?: string;
  scattering?: string;
}

/** Data freshness info for a data source */
export interface DataSourceFreshness {
  ageHours: number | null;
  lastUpdated: string | null;
  isStale: boolean;
}

/** Overall data freshness summary */
export interface DataFreshness {
  x402scan: DataSourceFreshness;
  scattering: DataSourceFreshness;
  nexflow: DataSourceFreshness;
}

export interface FacilitatorDebugInfo {
  id: string;
  name: string;
  baseUrl: string;
  networks: string[];
  score: number;
  rank: number;
  selected: boolean;
  x402scanScore?: number;
  confidence?: 'high' | 'medium' | 'low' | 'none';
  shortReason?: string;
  metrics?: FacilitatorMetrics;
  scattering?: ScatteringDisplayMetrics;
  nexflowTraffic?: NexFlowTrafficMetrics;
  reasons?: string[];
  externalLinks?: ExternalLinks;
}

export interface SmfFacilitatorsDebugResponse {
  ok: true;
  network: string | null;
  timeframe: FacilitatorPathMetricsTimeframe;
  usedFallback: boolean;
  recommended: string | null;
  recommendationReason: string;
  facilitators: FacilitatorDebugInfo[];
  dataFreshness: DataFreshness;
  timestamp: string;
}

export interface SmfFacilitatorsErrorResponse {
  ok: false;
  error: string;
}

export type SmfFacilitatorsResponse = SmfFacilitatorsDebugResponse | SmfFacilitatorsErrorResponse;

export interface FetchSmfFacilitatorsParams {
  network?: string;
  timeframe?: FacilitatorPathMetricsTimeframe;
}

// =============================================================================
// NETWORK OPTIONS
// =============================================================================

