// =============================================================================
// CLIENT
// =============================================================================

/**
 * Fetch SMF facilitators debug data from the API
 */
export async function fetchSmfFacilitatorsDebug(
  params: FetchSmfFacilitatorsParams = {}
): Promise<SmfFacilitatorsResponse> {
  try {
    const searchParams = new URLSearchParams();
    
    if (params.network) {
      searchParams.set('network', params.network);
    }
    if (params.timeframe) {
      searchParams.set('timeframe', params.timeframe);
    }

    const queryString = searchParams.toString();
    const url = `/api/debug/smf-facilitators${queryString ? `?${queryString}` : ''}`;

    const response = await fetch(url, {
      method: 'GET',
      headers: {
        'Content-Type': 'application/json',
      },
      cache: 'no-store',
    });

    const data = await response.json();

    if (!response.ok) {
      return {
        ok: false,
        error: data.error || `HTTP ${response.status}: ${response.statusText}`,
      };
    }

    return data as SmfFacilitatorsResponse;
  } catch (error) {
    return {
      ok: false,
      error: error instanceof Error ? error.message : 'Unknown error fetching SMF debug data',
    };
  }
}

// =============================================================================
// FORMATTERS
// =============================================================================

/**
 * Format a number as a percentage (e.g., 0.992 → "99.2%")
 */
export function formatPercent(value: number | undefined): string {
  if (value === undefined || value === null) return '-';
  return `${(value * 100).toFixed(1)}%`;
}

/**
 * Format latency in milliseconds (e.g., 142 → "142 ms")
 */
export function formatLatency(ms: number | undefined): string {
  if (ms === undefined || ms === null) return '-';
  return `${Math.round(ms)} ms`;
}

/**
 * Format invocation count (e.g., 12300 → "12.3K")
 */
export function formatInvocations(count: number | undefined): string {
  if (count === undefined || count === null) return '-';
  if (count >= 1_000_000) return `${(count / 1_000_000).toFixed(1)}M`;
  if (count >= 1_000) return `${(count / 1_000).toFixed(1)}K`;
  return count.toString();
}

/**
 * Format data age in hours (e.g., 2.1 → "2.1h ago")
 */
export function formatDataAge(hours: number | undefined): string {
  if (hours === undefined || hours === null) return '-';
  if (hours < 1) return `${Math.round(hours * 60)}m ago`;
  return `${hours.toFixed(1)}h ago`;
}

/**
 * Get confidence badge color class
 */
export function getConfidenceBadgeClass(confidence: string | undefined): string {
  switch (confidence) {
    case 'high':
      return 'bg-green-900/30 text-green-400 border-green-800';
    case 'medium':
      return 'bg-yellow-900/30 text-yellow-400 border-yellow-800';
    case 'low':
      return 'bg-orange-900/30 text-orange-400 border-orange-800';
    case 'none':
    default:
      return 'bg-gray-800 text-gray-400 border-gray-700';
  }
}

/**
 * Get row highlight class for selected facilitator
 */
export function getRowHighlightClass(selected: boolean): string {
  return selected
    ? 'bg-lime-900/20 border-lime-800'
    : 'bg-gray-900 border-gray-800';
}

// =============================================================================
// SCATTERING FORMATTERS
// =============================================================================

/**
 * Format USD volume (e.g., 12500 → "$12.5K")
 */
export function formatVolume(volume: number | undefined): string {
  if (volume === undefined || volume === null) return '-';
  if (volume >= 1_000_000) return `$${(volume / 1_000_000).toFixed(1)}M`;
  if (volume >= 1_000) return `$${(volume / 1_000).toFixed(1)}K`;
  return `$${volume.toFixed(0)}`;
}

/**
 * Format transaction count (e.g., 125000 → "125K")
 */
export function formatTxCount(count: number | undefined): string {
  if (count === undefined || count === null) return '-';
  if (count >= 1_000_000) return `${(count / 1_000_000).toFixed(1)}M`;
  if (count >= 1_000) return `${(count / 1_000).toFixed(0)}K`;
  return count.toString();
}

/**
 * Format change rate as percentage with + or - prefix
 */
export function formatChangeRate(rate: number | undefined): string {
  if (rate === undefined || rate === null) return '-';
  const prefix = rate >= 0 ? '+' : '';
  return `${prefix}${rate.toFixed(1)}%`;
}

/**
 * Get activity level badge color class
 */
export function getActivityLevelBadgeClass(level: ActivityLevel | undefined): string {
  switch (level) {
    case 'high':
      return 'bg-emerald-900/30 text-emerald-400 border-emerald-800';
    case 'medium':
      return 'bg-blue-900/30 text-blue-400 border-blue-800';
    case 'low':
      return 'bg-gray-800 text-gray-400 border-gray-700';
    case 'none':
    default:
      return 'bg-gray-900 text-gray-500 border-gray-800';
  }
}

/**
 * Get change rate color class (green for positive, red for negative)
 */
export function getChangeRateColorClass(rate: number | undefined): string {
  if (rate === undefined || rate === null) return 'text-gray-500';
  if (rate > 10) return 'text-green-400';
  if (rate > 0) return 'text-green-300';
  if (rate < -10) return 'text-red-400';
  if (rate < 0) return 'text-red-300';
  return 'text-gray-400';
}

/**
 * Get freshness status badge class
 */
export function getFreshnessBadgeClass(isStale: boolean): string {
  return isStale
    ? 'bg-yellow-900/30 text-yellow-400 border-yellow-800'
    : 'bg-green-900/30 text-green-400 border-green-800';
}

/**
 * Format timestamp as relative time (e.g., "2h ago", "just now")
 */
export function formatRelativeTime(timestamp: string | null | undefined): string {
  if (!timestamp) return 'never';
  
  const now = Date.now();
  const then = new Date(timestamp).getTime();
  const diffMs = now - then;
  
  const minutes = Math.floor(diffMs / (1000 * 60));
  const hours = Math.floor(diffMs / (1000 * 60 * 60));
  const days = Math.floor(diffMs / (1000 * 60 * 60 * 24));
  
  if (minutes < 1) return 'just now';
  if (minutes < 60) return `${minutes}m ago`;
  if (hours < 24) return `${hours}h ago`;
  return `${days}d ago`;
}

// =============================================================================
// LOCALSTORAGE HELPERS
// =============================================================================

const STORAGE_KEY = 'smf-dashboard-prefs';

export interface DashboardPrefs {
  network: string;
  timeframe: FacilitatorPathMetricsTimeframe;
}

/**
 * Load dashboard preferences from localStorage
 */
export function loadDashboardPrefs(): DashboardPrefs {
  if (typeof window === 'undefined') {
    return { network: 'eip155:8453', timeframe: '1d' };
  }
  
  try {
    const stored = localStorage.getItem(STORAGE_KEY);
    if (stored) {
      const prefs = JSON.parse(stored);
      return {
        network: prefs.network ?? 'eip155:8453',
        timeframe: prefs.timeframe ?? '1d',
      };
    }
  } catch (e) {
    // Ignore localStorage errors
  }
  
  return { network: 'eip155:8453', timeframe: '1d' };
}

/**
 * Save dashboard preferences to localStorage
 */
export function saveDashboardPrefs(prefs: DashboardPrefs): void {
  if (typeof window === 'undefined') return;
  
  try {
    localStorage.setItem(STORAGE_KEY, JSON.stringify(prefs));
  } catch (e) {
    // Ignore localStorage errors
  }
}

