'use client';

import { useState, useEffect, useCallback } from 'react';
import {
  Activity,
  AlertTriangle,
  CheckCircle2,
  Clock,
  RefreshCw,
  Server,
  Zap,
  TrendingUp,
  Shield,
  Globe,
  ExternalLink,
  BarChart3,
  Users,
  ArrowUpRight,
  ArrowDownRight,
  Database,
} from 'lucide-react';
import {
  fetchSmfFacilitatorsDebug,
  formatPercent,
  formatLatency,
  formatInvocations,
  formatDataAge,
  formatVolume,
  formatTxCount,
  formatChangeRate,
  formatRelativeTime,
  getConfidenceBadgeClass,
  getRowHighlightClass,
  getActivityLevelBadgeClass,
  getChangeRateColorClass,
  getFreshnessBadgeClass,
  loadDashboardPrefs,
  saveDashboardPrefs,
  NETWORK_OPTIONS,
  TIMEFRAME_OPTIONS,
  type SmfFacilitatorsDebugResponse,
  type FacilitatorDebugInfo,
  type FacilitatorPathMetricsTimeframe,
  type DataFreshness,
} from '@/lib/smf-debug-client';

// =============================================================================
// MAIN PAGE COMPONENT
// =============================================================================

export default function SmfDashboardPage() {
  // Load initial preferences from localStorage
  const [network, setNetwork] = useState<string>('eip155:8453');
  const [timeframe, setTimeframe] = useState<FacilitatorPathMetricsTimeframe>('1d');
  const [data, setData] = useState<SmfFacilitatorsDebugResponse | null>(null);
  const [error, setError] = useState<string | null>(null);
  const [loading, setLoading] = useState(true);
  const [lastRefresh, setLastRefresh] = useState<Date | null>(null);
  const [prefsLoaded, setPrefsLoaded] = useState(false);

  // Load preferences from localStorage on mount
  useEffect(() => {
    const prefs = loadDashboardPrefs();
    setNetwork(prefs.network);
    setTimeframe(prefs.timeframe);
    setPrefsLoaded(true);
  }, []);

  // Save preferences when they change
  useEffect(() => {
    if (prefsLoaded) {
      saveDashboardPrefs({ network, timeframe });
    }
  }, [network, timeframe, prefsLoaded]);

  const fetchData = useCallback(async () => {
    setLoading(true);
    setError(null);

    const response = await fetchSmfFacilitatorsDebug({
      network: network || undefined,
      timeframe,
    });

    if (!response.ok) {
      setError(response.error);
      setData(null);
    } else {
      setData(response);
      setError(null);
    }

    setLastRefresh(new Date());
    setLoading(false);
  }, [network, timeframe]);

  useEffect(() => {
    if (prefsLoaded) {
      fetchData();
    }
  }, [fetchData, prefsLoaded]);

  return (
    <div className="min-h-screen bg-gray-950 text-white">
      {/* Header */}
      <header className="border-b border-gray-800 bg-gray-900/50 backdrop-blur-sm sticky top-0 z-10">
        <div className="max-w-7xl mx-auto px-6 py-4">
          <div className="flex items-center justify-between">
            <div className="flex items-center gap-3">
              <div className="p-2 bg-gradient-to-br from-cyan-500 to-blue-600 rounded-lg">
                <Zap className="w-6 h-6 text-white" />
              </div>
              <div>
                <h1 className="text-xl font-bold">SMF Dashboard</h1>
                <p className="text-sm text-gray-400">Smart Meta-Facilitator Rankings</p>
              </div>
            </div>
            <div className="flex items-center gap-4">
              {lastRefresh && (
                <span className="text-xs text-gray-500">
                  Last updated: {lastRefresh.toLocaleTimeString()}
                </span>
              )}
              <button
                onClick={fetchData}
                disabled={loading}
                className="flex items-center gap-2 px-3 py-2 bg-gray-800 hover:bg-gray-700 rounded-lg text-sm font-medium transition-colors disabled:opacity-50"
              >
                <RefreshCw className={`w-4 h-4 ${loading ? 'animate-spin' : ''}`} />
                Refresh
              </button>
            </div>
          </div>
        </div>
      </header>

      <main className="max-w-7xl mx-auto px-6 py-8">
        {/* Controls */}
        <ControlsSection
          network={network}
          timeframe={timeframe}
          onNetworkChange={setNetwork}
          onTimeframeChange={setTimeframe}
        />

        {/* Error State */}
        {error && <ErrorBanner error={error} />}

        {/* Loading State */}
        {loading && !data && <LoadingState />}

        {/* Data Display */}
        {data && (
          <>
            {/* Data Freshness Panel */}
            <DataFreshnessPanel freshness={data.dataFreshness} />

            {/* Summary Panel */}
            <SummaryPanel data={data} />

            {/* Fallback Warning */}
            {data.usedFallback && <FallbackWarning />}

            {/* Facilitators Table */}
            <FacilitatorsTable facilitators={data.facilitators} />

            {/* Selected Facilitator Reasons */}
            <SelectedFacilitatorDetails
              facilitator={data.facilitators.find((f) => f.selected)}
            />
          </>
        )}
      </main>
    </div>
  );
}

// =============================================================================
// CONTROLS SECTION
// =============================================================================

interface ControlsSectionProps {
  network: string;
  timeframe: FacilitatorPathMetricsTimeframe;
  onNetworkChange: (network: string) => void;
  onTimeframeChange: (timeframe: FacilitatorPathMetricsTimeframe) => void;
}

function ControlsSection({
  network,
  timeframe,
  onNetworkChange,
  onTimeframeChange,
}: ControlsSectionProps) {
  return (
    <div className="mb-6 p-4 bg-gray-900 rounded-xl border border-gray-800">
      <div className="flex flex-wrap items-center gap-6">
        <div className="flex items-center gap-3">
          <Globe className="w-5 h-5 text-gray-400" />
          <label htmlFor="network" className="text-sm font-medium text-gray-300">
            Network
          </label>
          <select
            id="network"
            value={network}
            onChange={(e) => onNetworkChange(e.target.value)}
            className="px-3 py-2 bg-gray-800 border border-gray-700 rounded-lg text-white text-sm focus:outline-none focus:ring-2 focus:ring-cyan-500 focus:border-transparent"
          >
            {NETWORK_OPTIONS.map((opt) => (
              <option key={opt.value} value={opt.value}>
                {opt.label}
              </option>
            ))}
          </select>
        </div>

        <div className="flex items-center gap-3">
          <Clock className="w-5 h-5 text-gray-400" />
          <label htmlFor="timeframe" className="text-sm font-medium text-gray-300">
            Timeframe
          </label>
          <select
            id="timeframe"
            value={timeframe}
            onChange={(e) => onTimeframeChange(e.target.value as FacilitatorPathMetricsTimeframe)}
            className="px-3 py-2 bg-gray-800 border border-gray-700 rounded-lg text-white text-sm focus:outline-none focus:ring-2 focus:ring-cyan-500 focus:border-transparent"
          >
            {TIMEFRAME_OPTIONS.map((opt) => (
              <option key={opt.value} value={opt.value}>
                {opt.label}
              </option>
            ))}
          </select>
        </div>
      </div>
    </div>
  );
}

// =============================================================================
// DATA FRESHNESS PANEL
// =============================================================================

interface DataFreshnessPanelProps {
  freshness: DataFreshness;
}

function DataFreshnessPanel({ freshness }: DataFreshnessPanelProps) {
  const anyStale = freshness.x402scan.isStale || freshness.scattering.isStale;

  return (
    <div className="mb-6 p-4 bg-gray-900/50 rounded-xl border border-gray-800">
      <div className="flex items-center gap-2 mb-3">
        <Database className="w-4 h-4 text-gray-400" />
        <span className="text-sm font-medium text-gray-300">Data Sources</span>
        {anyStale && (
          <span className="ml-2 px-2 py-0.5 text-xs bg-yellow-900/30 text-yellow-400 border border-yellow-800 rounded">
            Some data may be stale
          </span>
        )}
      </div>
      
      <div className="grid grid-cols-1 md:grid-cols-3 gap-4">
        {/* x402scan */}
        <div className="flex items-center justify-between p-3 bg-gray-800/50 rounded-lg">
          <div className="flex items-center gap-2">
            <div className={`w-2 h-2 rounded-full ${freshness.x402scan.isStale ? 'bg-yellow-400' : 'bg-green-400'}`} />
            <span className="text-sm text-gray-300">x402scan</span>
          </div>
          <div className="text-right">
            <span className={`text-xs ${freshness.x402scan.isStale ? 'text-yellow-400' : 'text-gray-400'}`}>
              {freshness.x402scan.ageHours !== null ? `${freshness.x402scan.ageHours}h ago` : 'No data'}
            </span>
          </div>
        </div>

        {/* Scattering */}
        <div className="flex items-center justify-between p-3 bg-gray-800/50 rounded-lg">
          <div className="flex items-center gap-2">
            <div className={`w-2 h-2 rounded-full ${freshness.scattering.isStale ? 'bg-yellow-400' : 'bg-green-400'}`} />
            <span className="text-sm text-gray-300">Scattering</span>
          </div>
          <div className="text-right">
            <span className={`text-xs ${freshness.scattering.isStale ? 'text-yellow-400' : 'text-gray-400'}`}>
              {freshness.scattering.ageHours !== null ? `${freshness.scattering.ageHours}h ago` : 'No data'}
            </span>
          </div>
        </div>

        {/* NexFlow */}
        <div className="flex items-center justify-between p-3 bg-gray-800/50 rounded-lg">
          <div className="flex items-center gap-2">
            <div className={`w-2 h-2 rounded-full ${freshness.nexflow.isStale ? 'bg-gray-600' : 'bg-green-400'}`} />
            <span className="text-sm text-gray-300">NexFlow</span>
          </div>
          <div className="text-right">
            <span className="text-xs text-gray-500">
              {freshness.nexflow.lastUpdated ? formatRelativeTime(freshness.nexflow.lastUpdated) : 'Pending'}
            </span>
          </div>
        </div>
      </div>
    </div>
  );
}

// =============================================================================
// SUMMARY PANEL
// =============================================================================

interface SummaryPanelProps {
  data: SmfFacilitatorsDebugResponse;
}

function SummaryPanel({ data }: SummaryPanelProps) {
  const selectedFacilitator = data.facilitators.find((f) => f.selected);

  return (
    <div className="mb-6 grid grid-cols-1 md:grid-cols-4 gap-4">
      {/* Network & Timeframe */}
      <div className="p-4 bg-gray-900 rounded-xl border border-gray-800">
        <div className="flex items-center gap-2 mb-2">
          <Globe className="w-4 h-4 text-cyan-400" />
          <span className="text-sm font-medium text-gray-400">Configuration</span>
        </div>
        <p className="text-lg font-semibold text-white">
          {data.network || 'All Networks'}
        </p>
        <p className="text-sm text-gray-500">Timeframe: {data.timeframe}</p>
      </div>

      {/* Recommended Facilitator */}
      <div className="p-4 bg-gray-900 rounded-xl border border-gray-800">
        <div className="flex items-center gap-2 mb-2">
          <CheckCircle2 className="w-4 h-4 text-lime-400" />
          <span className="text-sm font-medium text-gray-400">Recommended</span>
        </div>
        <p className="text-lg font-semibold text-white">
          {data.recommended || 'None'}
        </p>
        <p className="text-sm text-gray-500">
          {data.usedFallback ? (
            <span className="text-yellow-400">Using fallback</span>
          ) : (
            <span className="text-green-400">Metrics-based</span>
          )}
        </p>
      </div>

      {/* Score */}
      <div className="p-4 bg-gray-900 rounded-xl border border-gray-800">
        <div className="flex items-center gap-2 mb-2">
          <TrendingUp className="w-4 h-4 text-blue-400" />
          <span className="text-sm font-medium text-gray-400">SMF Score</span>
        </div>
        <p className="text-lg font-semibold text-white">
          {selectedFacilitator?.score.toFixed(1) || '-'}
        </p>
        <p className="text-sm text-gray-500">
          x402scan: {selectedFacilitator?.x402scanScore ?? '-'}
        </p>
      </div>

      {/* Scattering Activity */}
      <div className="p-4 bg-gray-900 rounded-xl border border-gray-800">
        <div className="flex items-center gap-2 mb-2">
          <BarChart3 className="w-4 h-4 text-emerald-400" />
          <span className="text-sm font-medium text-gray-400">Activity Score</span>
        </div>
        <p className="text-lg font-semibold text-white">
          {selectedFacilitator?.scattering?.activityScore ?? '-'}
        </p>
        <p className="text-sm text-gray-500">
          {selectedFacilitator?.scattering ? (
            <span className={getChangeRateColorClass(selectedFacilitator.scattering.volumeChangeRate)}>
              {formatChangeRate(selectedFacilitator.scattering.volumeChangeRate)} vol
            </span>
          ) : (
            'No Scattering data'
          )}
        </p>
      </div>

      {/* Recommendation Reason - Full Width */}
      <div className="md:col-span-4 p-4 bg-gradient-to-r from-cyan-900/20 to-blue-900/20 rounded-xl border border-cyan-800/50">
        <div className="flex items-start gap-3">
          <Shield className="w-5 h-5 text-cyan-400 mt-0.5 flex-shrink-0" />
          <div>
            <span className="text-sm font-medium text-cyan-300">Routing Reason</span>
            <p className="text-gray-300 mt-1">{data.recommendationReason}</p>
          </div>
        </div>
      </div>
    </div>
  );
}

// =============================================================================
// FALLBACK WARNING
// =============================================================================

function FallbackWarning() {
  return (
    <div className="mb-6 p-4 bg-yellow-900/20 border border-yellow-800 rounded-xl flex items-center gap-3">
      <AlertTriangle className="w-5 h-5 text-yellow-400 flex-shrink-0" />
      <div>
        <span className="font-medium text-yellow-400">Using Fallback Ordering</span>
        <p className="text-sm text-yellow-300/70 mt-0.5">
          No trusted x402scan data available. Router may use default facilitator priority.
        </p>
      </div>
    </div>
  );
}

// =============================================================================
// ERROR BANNER
// =============================================================================

function ErrorBanner({ error }: { error: string }) {
  return (
    <div className="mb-6 p-4 bg-red-900/20 border border-red-800 rounded-xl flex items-center gap-3">
      <AlertTriangle className="w-5 h-5 text-red-400 flex-shrink-0" />
      <div>
        <span className="font-medium text-red-400">Failed to Load SMF Debug Data</span>
        <p className="text-sm text-red-300/70 mt-0.5">{error}</p>
      </div>
    </div>
  );
}

// =============================================================================
// LOADING STATE
// =============================================================================

function LoadingState() {
  return (
    <div className="flex flex-col items-center justify-center py-16">
      <div className="relative">
        <div className="w-16 h-16 rounded-full border-4 border-gray-800"></div>
        <div className="absolute top-0 left-0 w-16 h-16 rounded-full border-4 border-cyan-500 border-t-transparent animate-spin"></div>
      </div>
      <p className="mt-4 text-gray-400">Loading facilitator data...</p>
    </div>
  );
}

// =============================================================================
// FACILITATORS TABLE
// =============================================================================

interface FacilitatorsTableProps {
  facilitators: FacilitatorDebugInfo[];
}

function FacilitatorsTable({ facilitators }: FacilitatorsTableProps) {
  if (facilitators.length === 0) {
    return (
      <div className="text-center py-12 bg-gray-900 rounded-xl border border-gray-800">
        <Server className="w-12 h-12 text-gray-600 mx-auto mb-4" />
        <p className="text-gray-400">No facilitators found for this network</p>
      </div>
    );
  }

  return (
    <div className="mb-8 overflow-hidden rounded-xl border border-gray-800">
      <div className="overflow-x-auto">
        <table className="w-full">
          <thead className="bg-gray-900/80">
            <tr className="text-left text-sm text-gray-400">
              <th className="px-4 py-3 font-medium">Rank</th>
              <th className="px-4 py-3 font-medium">Facilitator</th>
              <th className="px-4 py-3 font-medium text-right">SMF Score</th>
              <th className="px-4 py-3 font-medium text-center">Activity</th>
              <th className="px-4 py-3 font-medium text-right">3d Volume</th>
              <th className="px-4 py-3 font-medium text-right">3d Txns</th>
              <th className="px-4 py-3 font-medium text-center">Confidence</th>
              <th className="px-4 py-3 font-medium text-right">Success</th>
              <th className="px-4 py-3 font-medium text-right">p95 Latency</th>
              <th className="px-4 py-3 font-medium text-right">Invocations</th>
              <th className="px-4 py-3 font-medium text-center">Links</th>
            </tr>
          </thead>
          <tbody className="divide-y divide-gray-800">
            {facilitators.map((facilitator) => (
              <FacilitatorRow key={facilitator.id} facilitator={facilitator} />
            ))}
          </tbody>
        </table>
      </div>
    </div>
  );
}

// =============================================================================
// FACILITATOR ROW
// =============================================================================

interface FacilitatorRowProps {
  facilitator: FacilitatorDebugInfo;
}

function FacilitatorRow({ facilitator }: FacilitatorRowProps) {
  return (
    <tr
      className={`${getRowHighlightClass(facilitator.selected)} transition-colors hover:bg-gray-800/50`}
    >
      {/* Rank */}
      <td className="px-4 py-3">
        <div className="flex items-center gap-2">
          <span
            className={`w-7 h-7 flex items-center justify-center rounded-full text-sm font-bold ${
              facilitator.rank === 1
                ? 'bg-lime-500 text-gray-900'
                : facilitator.rank === 2
                ? 'bg-gray-600 text-white'
                : facilitator.rank === 3
                ? 'bg-amber-700 text-white'
                : 'bg-gray-800 text-gray-400'
            }`}
          >
            {facilitator.rank}
          </span>
        </div>
      </td>

      {/* Facilitator */}
      <td className="px-4 py-3">
        <div className="flex items-center gap-3">
          {facilitator.selected && (
            <CheckCircle2 className="w-4 h-4 text-lime-400 flex-shrink-0" />
          )}
          <div>
            <p className="font-medium text-white">{facilitator.name}</p>
            <p className="text-xs text-gray-500 font-mono">{facilitator.id}</p>
          </div>
        </div>
      </td>

      {/* SMF Score */}
      <td className="px-4 py-3 text-right">
        <span className="font-mono font-semibold text-white">
          {facilitator.score.toFixed(1)}
        </span>
      </td>

      {/* Activity Level (Scattering) */}
      <td className="px-4 py-3 text-center">
        <span
          className={`inline-flex px-2 py-1 text-xs font-medium rounded border ${getActivityLevelBadgeClass(
            facilitator.scattering?.activityLevel
          )}`}
        >
          {facilitator.scattering?.activityLevel || 'none'}
        </span>
      </td>

      {/* 3d Volume */}
      <td className="px-4 py-3 text-right">
        <div className="flex flex-col items-end">
          <span className="font-mono text-emerald-400">
            {formatVolume(facilitator.scattering?.volumeUsd3d)}
          </span>
          {facilitator.scattering?.volumeChangeRate !== undefined && (
            <span className={`text-xs flex items-center gap-0.5 ${getChangeRateColorClass(facilitator.scattering.volumeChangeRate)}`}>
              {facilitator.scattering.volumeChangeRate >= 0 ? (
                <ArrowUpRight className="w-3 h-3" />
              ) : (
                <ArrowDownRight className="w-3 h-3" />
              )}
              {Math.abs(facilitator.scattering.volumeChangeRate).toFixed(0)}%
            </span>
          )}
        </div>
      </td>

      {/* 3d Txns */}
      <td className="px-4 py-3 text-right">
        <div className="flex flex-col items-end">
          <span className="font-mono text-blue-400">
            {formatTxCount(facilitator.scattering?.txCount3d)}
          </span>
          {facilitator.scattering?.uniqueBuyers3d !== undefined && facilitator.scattering.uniqueBuyers3d > 0 && (
            <span className="text-xs text-gray-500 flex items-center gap-1">
              <Users className="w-3 h-3" />
              {formatTxCount(facilitator.scattering.uniqueBuyers3d)}
            </span>
          )}
        </div>
      </td>

      {/* Confidence */}
      <td className="px-4 py-3 text-center">
        <span
          className={`inline-flex px-2 py-1 text-xs font-medium rounded border ${getConfidenceBadgeClass(
            facilitator.confidence
          )}`}
        >
          {facilitator.confidence || 'none'}
        </span>
      </td>

      {/* Success Rate */}
      <td className="px-4 py-3 text-right">
        <span
          className={`font-mono ${
            facilitator.metrics?.successRate !== undefined
              ? facilitator.metrics.successRate >= 0.99
                ? 'text-green-400'
                : facilitator.metrics.successRate >= 0.95
                ? 'text-yellow-400'
                : 'text-red-400'
              : 'text-gray-500'
          }`}
        >
          {formatPercent(facilitator.metrics?.successRate)}
        </span>
      </td>

      {/* p95 Latency */}
      <td className="px-4 py-3 text-right">
        <span
          className={`font-mono ${
            facilitator.metrics?.p95LatencyMs !== undefined
              ? facilitator.metrics.p95LatencyMs < 200
                ? 'text-green-400'
                : facilitator.metrics.p95LatencyMs < 500
                ? 'text-yellow-400'
                : 'text-red-400'
              : 'text-gray-500'
          }`}
        >
          {formatLatency(facilitator.metrics?.p95LatencyMs)}
        </span>
      </td>

      {/* Invocations */}
      <td className="px-4 py-3 text-right">
        <span className="font-mono text-gray-300">
          {formatInvocations(facilitator.metrics?.invocations)}
        </span>
      </td>

      {/* External Links */}
      <td className="px-4 py-3 text-center">
        <div className="flex items-center justify-center gap-2">
          {facilitator.externalLinks?.x402scan && (
            <a
              href={facilitator.externalLinks.x402scan}
              target="_blank"
              rel="noopener noreferrer"
              className="p-1 text-gray-500 hover:text-cyan-400 transition-colors"
              title="View on x402scan"
            >
              <ExternalLink className="w-4 h-4" />
            </a>
          )}
          {facilitator.externalLinks?.scattering && (
            <a
              href={facilitator.externalLinks.scattering}
              target="_blank"
              rel="noopener noreferrer"
              className="p-1 text-gray-500 hover:text-emerald-400 transition-colors"
              title="View on Scattering"
            >
              <BarChart3 className="w-4 h-4" />
            </a>
          )}
        </div>
      </td>
    </tr>
  );
}

// =============================================================================
// SELECTED FACILITATOR DETAILS
// =============================================================================

interface SelectedFacilitatorDetailsProps {
  facilitator: FacilitatorDebugInfo | undefined;
}

function SelectedFacilitatorDetails({ facilitator }: SelectedFacilitatorDetailsProps) {
  if (!facilitator) return null;

  return (
    <div className="p-6 bg-gray-900 rounded-xl border border-gray-800">
      <div className="flex items-center gap-2 mb-4">
        <Activity className="w-5 h-5 text-lime-400" />
        <h3 className="text-lg font-semibold">
          Selected Facilitator: {facilitator.name}
        </h3>
      </div>

      {/* Short Reason */}
      {facilitator.shortReason && (
        <div className="mb-4 p-3 bg-gray-800 rounded-lg">
          <p className="text-sm text-gray-300">{facilitator.shortReason}</p>
        </div>
      )}

      {/* Metrics Grid */}
      <div className="grid grid-cols-2 md:grid-cols-4 gap-4 mb-4">
        {/* x402scan Metrics */}
        {facilitator.metrics && (
          <>
            <div className="p-3 bg-gray-800/50 rounded-lg">
              <p className="text-xs text-gray-500 mb-1">Success Rate</p>
              <p className="text-lg font-mono text-green-400">{formatPercent(facilitator.metrics.successRate)}</p>
            </div>
            <div className="p-3 bg-gray-800/50 rounded-lg">
              <p className="text-xs text-gray-500 mb-1">p95 Latency</p>
              <p className="text-lg font-mono text-cyan-400">{formatLatency(facilitator.metrics.p95LatencyMs)}</p>
            </div>
          </>
        )}
        
        {/* Scattering Metrics */}
        {facilitator.scattering && (
          <>
            <div className="p-3 bg-gray-800/50 rounded-lg">
              <p className="text-xs text-gray-500 mb-1">3d Volume</p>
              <p className="text-lg font-mono text-emerald-400">{formatVolume(facilitator.scattering.volumeUsd3d)}</p>
            </div>
            <div className="p-3 bg-gray-800/50 rounded-lg">
              <p className="text-xs text-gray-500 mb-1">Unique Buyers</p>
              <p className="text-lg font-mono text-blue-400">{formatTxCount(facilitator.scattering.uniqueBuyers3d)}</p>
            </div>
          </>
        )}
      </div>

      {/* NexFlow Traffic (placeholder) */}
      {facilitator.nexflowTraffic && (
        <div className="mb-4 p-3 bg-gray-800/30 rounded-lg border border-gray-700">
          <div className="flex items-center gap-2 mb-2">
            <Zap className="w-4 h-4 text-gray-500" />
            <span className="text-sm font-medium text-gray-400">NexFlow Traffic (24h)</span>
          </div>
          <div className="grid grid-cols-3 gap-4 text-center">
            <div>
              <p className="text-xs text-gray-500">Total</p>
              <p className="font-mono text-gray-400">{facilitator.nexflowTraffic.last24hInvocations}</p>
            </div>
            <div>
              <p className="text-xs text-gray-500">Dogfood</p>
              <p className="font-mono text-gray-400">{facilitator.nexflowTraffic.last24hDogfoodInvocations}</p>
            </div>
            <div>
              <p className="text-xs text-gray-500">External</p>
              <p className="font-mono text-gray-400">{facilitator.nexflowTraffic.last24hExternalInvocations}</p>
            </div>
          </div>
        </div>
      )}

      {/* Detailed Reasons */}
      {facilitator.reasons && facilitator.reasons.length > 0 && (
        <div>
          <h4 className="text-sm font-medium text-gray-400 mb-2">Scoring Factors</h4>
          <ul className="grid grid-cols-2 md:grid-cols-3 lg:grid-cols-4 gap-2">
            {facilitator.reasons.map((reason, idx) => (
              <li
                key={idx}
                className="px-2 py-1.5 text-xs font-mono bg-gray-800 rounded text-gray-400"
              >
                {reason}
              </li>
            ))}
          </ul>
        </div>
      )}

      {/* Base URL & Links */}
      <div className="mt-4 pt-4 border-t border-gray-800 flex items-center justify-between">
        <div>
          <span className="text-xs text-gray-500">Base URL: </span>
          <code className="text-xs text-gray-400 font-mono">{facilitator.baseUrl}</code>
        </div>
        <div className="flex items-center gap-2">
          {facilitator.externalLinks?.x402scan && (
            <a
              href={facilitator.externalLinks.x402scan}
              target="_blank"
              rel="noopener noreferrer"
              className="flex items-center gap-1 px-2 py-1 text-xs bg-gray-800 hover:bg-gray-700 rounded text-cyan-400"
            >
              x402scan <ExternalLink className="w-3 h-3" />
            </a>
          )}
          {facilitator.externalLinks?.scattering && (
            <a
              href={facilitator.externalLinks.scattering}
              target="_blank"
              rel="noopener noreferrer"
              className="flex items-center gap-1 px-2 py-1 text-xs bg-gray-800 hover:bg-gray-700 rounded text-emerald-400"
            >
              Scattering <ExternalLink className="w-3 h-3" />
            </a>
          )}
        </div>
      </div>
    </div>
  );
}
